<template>
  <div id="MCTable"
  >
    {{ closeBodyDDM }}
<!--    :style="{background:style.body.background,color:style.body.color, fill:style.body.color}"-->
    <MC__tableHeader
      :titleTable="worksFields"
      :size-table="sizeTable"
      @SORT="sortParrent"

    />
    <div
      ref="styleSort"
      class="style-sort"></div>
    <MC__tableBody
      :data-table-body="filterList.slice(activePage * numberRow - numberRow, activePage * numberRow )"
      :table-size="sizeTable"
      :showMenu="showMenu"
      @dataDDM="dataDDMParrent"
      :number-row="numberRow"
    />
    <MC__pagination
      :number-pages="numberPagesCD"
      :active-page="activePage"
      @activePage="activePageParrent"
    />
    <!--      :TODO   id -->
    <drop-down-menu
      v-if="showMenu"
      :settings="settings"
      :position-d-d-m="dataMenu.position"
      :data-d-d-m="dataMenu.info"
      @actionDDM="actionsDDParrent"
    />
    <MC__card
      v-if="showCard"
      :type-action="typeActionsCard"
      :data-card="dataCard"
      :type-card="nameTable"
      @closeModalCard="closeModalCardParrent"
    />
    <MC__formMessage
      v-if="showFormMessage"
      :position-modal-message="{x:150, y:150}"
      :data-who-caused="dataCard"
      :name-id-recipient="nameIdRecipient"
      @closeFormMessage="closeFormMessageParrent"
      @toSendMessage="toSendMessageParrent"
    />
  </div>
</template>

<script>
    import MC__tableHeader from "./table-header/MC__table-header";
    import MC__tableBody from "./table-body/MC__table-body";
    import MC__pagination from "./pagination/MC__pagination";
    import DropDownMenu from "../drop-down-menu/drop-down-menu";

    import deleteRow from "./api/deleteRow/deleteRow";
    import MC__card from "../card/MC__card";
    import MC__formMessage from "../formMessage/formMessage";

    /**
     *   
     *    :
     *                          -   (MC__tableHeader)
     *                          -   ()
     *                          -   ()
     *
     *  :
     *                   -         ,
     *                   -            (        )
     *                   -         : [100,500,300,400,500]       
     *
     *
     * @param props:
     *              - dataTable                   -      
     *              - sizeTable                   -    (   ,   ,  )  ,        
     *              - settings                    -     (     : TODO   )
     *              - numberRow                   -    
     *              - nameTable                   -       (  ,          )
     *
     *
     * @param data:
     *              - filterName                  -     (       )
     *              - status                      -  ,   / 
     *              - activePage                  -  
     *              - showMenu                    -      
     *              - dataMenu                    -     
     *
     *              - showCard:                   -     
     *              - typeActionsCard             -  
     *              - dataCard                    -       
     *
     *              - showFormMessage             -       
     *
     *
     *
     *
     *                
                   <MC__table
     :data-table="dataTableTest"
     :name-table="nameTable"
     :size-table="[100,100, 250, 300, 300, 350, 350, 350]"
     :number-row="10"
     :settings="['','','','','']"
     />
     *
     */
    export default {
        name: "MC__table",
        components: {MC__formMessage, MC__card, DropDownMenu, MC__pagination, MC__tableBody, MC__tableHeader},
        props: {
            dataTable: {
                type: Array,
                default: () => {
                    return []
                }
            },
            sizeTable: {
                type: Array,
                default: () => []
            },
            settings: {
                type: Array,
                default: () => []
            },
            numberRow: {
                type: Number,
                default: 10
            },
            nameTable: {type: Object, default: () => {}},
            nameIdRecipient: {type:String, default: 'id_user'} //       

        },
        data() {
            return {
                filterName: {title: '', status: ''},
                status: false,
                activePage: 1,
                showMenu: false,
                dataMenu: {},
                //   
                showCard: false,
                typeActionsCard: '',
                dataCard: {},
                //     
                showFormMessage: false,
                typeMessage: ''
            }
        },
        computed: {
            /**
             *   
             * @returns {[]}
             */
            worksFields() {
                let keys = [];
                for (let item in this.dataTable[0]) keys.push(item)
                return keys
            },
            filterList() {
                let filterArray = [];
                filterArray = this.dataTable.slice() // 
                if (this.filterName.title === '') {
                    return this.dataTable;
                } else {
                    if (typeof filterArray[0][this.filterName.title] === 'number') {
                        if (this.filterName.status === 'up') {
                            filterArray = filterArray.sort((a, b) => {
                                return a[this.filterName.title] - b[this.filterName.title]
                            })
                        } else if (this.filterName.status === 'down') {
                            filterArray = filterArray.sort((a, b) => {
                                return b[this.filterName.title] - a[this.filterName.title]
                            })
                        } else {
                            filterArray = this.dataTable
                        }

                    } else {
                        filterArray = filterArray.sort((a, b) => {
                            let nameA = a[this.filterName.title] !== null ? a[this.filterName.title].toLowerCase() : '',
                                nameB = b[this.filterName.title] !== null ? b[this.filterName.title].toLowerCase() : '';
                            if (this.filterName.status === 'up') {
                                if (nameA < nameB) return -1;
                                if (nameA > nameB) return 1;
                                else return 0
                            } else if (this.filterName.status === 'down') {
                                if (nameA > nameB) return -1;
                                if (nameA < nameB) return 1;
                                else return 0
                            } else {
                                filterArray = this.dataTable
                            }
                        })
                    }
                    return filterArray
                }
            },
            /**
             *     
             * @returns {number}
             */
            numberPagesCD() {
                return Math.ceil(this.dataTable.length / this.numberRow)
            },
            /**
             *             
             */
            closeBodyDDM() {
                document.body.addEventListener('click', (e) => {
                    if (this.showMenu) {
                        if (!e.target.classList.contains('tbr')) this.showMenu = false
                    }
                })
            },
            style() {
                // return this.$store.getters.ACTIVETHEME
            }
        },
        methods: {
            /**
             *  
             * @param message -   
             */
            confirmAlert(message) {
                let result = confirm(message)
                return result
            },
            /**
             *        
             */
            sortParrent(data) {
                this.filterName = data
            },
            /**
             *     
             * @param data
             */
            activePageParrent(data) {
                this.activePage = data.page
            },
            /**
             *          
             */
            dataDDMParrent(data) {
                this.dataMenu = data;
                this.showMenu = true
            },
            /**
             *       
             * @param data {action:'EVENT  -  ', data.el.id}
             *
             *    ,   
             *
             */
            actionsDDParrent(data) {
                let activerow = '';
                this.dataTable.forEach(el => {
                    el.id === data.el.id ? activerow = el : ''
                });
                switch (data.action) {
                    case '':
                        if (this.confirmAlert('   ,   ')) {
                            this.deleteRow({tableName: this.nameTable, id: data.el.id, row: activerow})
                        }
                        break;
                    case '' || 'change' :
                        this.typeActionsCard = 'change';
                        this.showCard = true;
                        this.dataCard = activerow
                        break;
                    case '' || 'view':
                        console.log('    - ');
                        this.typeActionsCard = 'view';
                        this.showCard = true;
                        this.dataCard = activerow
                        console.log(this.dataCard)
                        break;
                    case '':
                        this.showFormMessage = true;
                        this.dataCard = activerow;
                        break;
                    default :
                        alert(',       :))')
                        break;
                }
            },
            /**
             *     
             * @param data - {tableName:data.tableName,id:data.id}
             */
            async deleteRow(data) {
                const res = await deleteRow.deleteFile(data)
                if (res) {
                    if (res.data) {
                        //   ,    
                        this.dataTable.forEach((el, i) => el === data.row ? this.dataTable.splice(i, 1) : '')
                    }
                }
            },
            /////////////////////////////////////////    
            /**
             *      
             */
            closeModalCardParrent() {
                this.showCard = false
            },

            ////////////////////////////////////////////     
            /**
             *     
             */
            closeFormMessageParrent() {
                this.showFormMessage = false
            },
            //      
            toSendMessageParrent(data) {
                console.log(data)
            }

        },
        beforeUpdate() {
            // console.log(this.$store.getters.ACTIVETHEME.body)
            if (this.sizeTable.length > 0) {
                this.$refs.styleSort.style.width = `${this.sizeTable.reduce((start, end) => {
                    return start + end
                })}px`
            } else {
                this.$refs.styleSort.style.width = `100%`
            }
        },
        updated() {
            setTimeout(() => {
                this.$refs.styleSort.style.width = `0`
            }, 1000)

        }
    }
</script>

<style lang="less">
  #MCTable {
    min-width: 100%;
    width: auto;
    min-height: 100%;
    height: auto;
    overflow-x: auto;

    .style-sort {
      width: 0;
      height: 1px;
      background: #2574CF;
      transition: .2s ease-in-out;
    }
  }


  #MCTable::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    border-radius: 1px;
    background-color: #ebf0f5;
  }

  #MCTable::-webkit-scrollbar {
    width: 1px;
    height: 8px;
    background-color: #F5F5F5;
  }

  #MCTable::-webkit-scrollbar-thumb {
    border-radius: 4px;
    -webkit-box-shadow: inset 0 0 6px rgba(151, 153, 156, 0.3);
    background-color: #565657;
  }
</style>



