<template>
    <div id="MCTableBody" :style="{height: numberRow * 35+'px'}">
      <MC__tableBodyRow
        v-for="(list, i) in dataTableBody"
        :key="`row-${i}`"
        :data-table-body-row="list"
        :table-size="tableSize"
        :id-row="list.id"
        @activeRowSettings="activeRowSettingsParrent"
      />
    </div>
</template>

<script>
    /**
     *   
     *
     * @param props:
     *              - dataTableBody                -  
     *              - tableSize                    -  
     *              - numberRow                    -      ,      
     *
     *
     * @param data:
     *
     *
     */
    import MC__tableBodyRow from "./MC__table-body-row";
    import DropDownMenu from "../../drop-down-menu/drop-down-menu";
    export default {
        name: "MC__table-body",
        components: {DropDownMenu, MC__tableBodyRow},
        props: {
            dataTableBody: {
                type: Array,
                 default: ()=> []
            },
            tableSize: {
                type: Array,
                default: () => []
            },
            numberRow: {Number}
        },
        methods: {
            /**
             *         
             * @param data - id ()
             */
            activeRowSettingsParrent(data) {
                this.$emit('dataDDM', {
                    position: data.position,
                    info: data.el
                })
            },
        },
    }
</script>

<style lang="less">
#MCTableBody {
  display: flex;
  flex-direction: column;
}
</style>



