<!--  -->
<template>
  <div id="slider" ref="slider">
    <div class="slider" v-if="dataInfo">
      <slider__header :title="dataInfo.title"/>
      <slider-block
        :info="dataInfo.data"
        :navigation="navigationSlider"
        :carScrolling="carScrolling"
      />

    </div>
  </div>
</template>

<script>
    import Slider__header from "./slider__header";
    import SliderBlock from "./sliderBlock";
    /**
     *   
     * - sizeSlider =   ( , , )  - typeSize big, half, third.  big
     * @param props
     * dataInfo         -      
     * navigation       - boolean  ,    
     * carScrolling     - boolean ,    
     *
     * @param data
     * sizeSlider     -   ,    ,     
     * buttNavigation -    buttonNavigation   
     * */
    export default {
        name: "slider",
        components: {SliderBlock, Slider__header},
        props: {
            dataInfo: Object,
            navigation: Boolean,
            carScrolling: Boolean,
        },
        data() {
            return {
                dataInfoSlider: {},
                navigationSlider: true,
            }
        },
        methods: {
          init() {
              this.dataInfoSlider = this.dataInfo;
              this.navigationSlider = this.navigation;
          }
        },
        mounted() {
            this.init()
        }
    }
</script>

<style lang="less">

  #slider {
    height: auto;
    max-height: 600px;
    width: 100%;

    .slider {
      width: 100%;
      height: 100%;
    }
  }

</style>



