import api from './../api'

/**
 *  
 *
 */

/**
 *   
 *  :
 *           -   - recipient
 *           -   - info
 *           -    - from
 *
 *
 *    
         app.post('/addMessage', function(req,res) {
         console.log(req.body)
         res.send({status:true, message:' ',typeMessage:'success'})
     })
 *
 *        INSERT INTO `message` (`id`, `id_from`, `fio_from`, `id_recipient`, `theme`, `description`, `status`) VALUES (NULL, '82', '  ', '107', '   4584', '      ', '0');
 *
 *
 */
export default {
  addMessage (data) {
    return api().post('/addMessage',{from:data.from,recipient_id: data.recipient_id, info: data.info })
  }
}



