const state     = {
    /**
     *  :
     *   this.$store.commit('SET_LOADER',true)
     *    this.$store.commit('SET_LOADER',false)
     *  
     * LOADER() {
     *           return this.$store.getters.LOADER
     *       }
     *
     */
    loader: false,
  };
const getters   = {
    /**
     *    
     * @param state -    
     * @returns {*} -   
     * @constructor
     */
    LOADER: state => {
      return state.loader
    },
  };
const mutations = {
    SET_LOADER: (state, bool) => {
      state.loader = bool
    },
  };
const actions   = {};

export default {state, getters, mutations, actions}



