
const state     = {
    /**
     *        
     */
    menuInfoUser: false,
    /**
     *   
     *     
     */
    switcherMenu: [],
  };
const getters   = {
    /**
     *      
     * @param state
     * @returns {state.menuInfoUser|{open}}
     * @constructor
     */
    MENUINFOUSER: state => {
      return state.menuInfoUser;
    },
    /**
     *      
     * @param state
     * @returns {[]}
     * @constructor
     */
    SWITCHERMENU: state => {
      return state.switcherMenu
    },
  };
const mutations = {
    /**
     *  /   
     * @param state
     * @param open
     * @constructor
     */
    SET_MENUINFOUSER: (state, open) => {
      state.menuInfoUser = open
    },
    /**
     *   
     * @param state  -  
     * @param block -  
     * @constructor
     */
    SET_SWITCHERMENU: (state, block) => {
      state.switcherMenu.push(block)
    },
    /**
     *     
     * @param state
     * @param i -     
     * @constructor
     */
    SET_SWITCHMENUACTIVE: (state, i) => {
      state.switcherMenu.forEach((el, it) => {
        if (i !== null) {
          if (i === it) {
            el.sidebar = !el.sidebar
          } else {
            el.sidebar = false
          }
        } else {
          el.sidebar = false
        }
      })
    },
    /**
     *    
     * @param state
     * @constructor
     */
    SET_SWITCHERMENURESSET: (state) => {
      state.switcherMenu = []
    },
  };
const actions   = {};

export default {state, getters, mutations, actions}



